import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { DialogService } from 'ng2-bootstrap-modal';

/* Components */
import { PopoverLoginComponent } from '../../modals/popover-login/popover-login.component';

/* Services */
import { ProjectsService } from '../../../Services/Projects/projects.service';
import { ProfilesService } from '../../../Services/Profiles/profiles.service';
import { Activity, NotificationService } from '../../../Services/notification.service';
import { PicturesService } from '../../../Services/Pictures/pictures.service';
import { TokenService } from '../../../Services/Authentication/token.service';

/* Interfaces */
import { ProjectCardModel, SearchModel } from '../../../Interfaces/search-models';
import { homeCategories } from '../../../Interfaces/Constants/categories-constant';
import { searchProfile } from '../../../Interfaces/Constants/search-profile.constant';
import { searchProject2, skillTrending } from '../../../Interfaces/Constants/search-project.constant';

/* Animations */
import { slideActivities } from '../../../Animations/animations';

@Component({
	selector: 'app-home-trending',
	templateUrl: './home-trending.component.html',
	styleUrls: [
		'./home-trending.component.scss',
		'../../../../public/styles/project-card.scss',
		'../../../../public/styles/login-signup-modal.scss'
	],
	// host: {
	// 	'[@slideLeft]' : 'left1'
	// },
	animations: [slideActivities],
	providers: [NotificationService]
})
export class HomeTrendingComponent implements OnInit, OnDestroy {

	@Input() mobile 	: boolean
	@Input() rank 		: number
	@Input() login 		: number
	bestProject			: ProjectCardModel
	categories 			: Object
	searchProjectBody 	: SearchModel
	searchProfileBody 	: SearchModel
	skillProfiles		: Object[] = []
	currentProfiles 	: any
	skillTrending 		: Array<string> = skillTrending
	currentCategory 	: string
	currentSkill 		: string
	count 				: number = 0
	countSlide 			: number = 0
	slideState 			: string = 'left1'
	interval1 			: any
	interval2 			: any

	config: Object = {
		direction             : 'horizontal',
		speed                 : 400,
		autoplay              : 8000,
		spaceBetween          : 30,
		loop                  : true,
		pagination            : '.swiper-pagination',
		paginationClickable   : true,
		paginationHide        : false,
	}

	private activities: Activity[];

	constructor(private ProjectsService: ProjectsService,
				private ProfilesService: ProfilesService,
				private NotificationService: NotificationService,
				private PicturesService: PicturesService,
				private TokenService: TokenService,
				private dialogService: DialogService) {
    	this.moveSliders();
		this.getRecentActivities();
	}

	ngOnInit() {
		this.searchProjectBody 	= searchProject2
		this.categories 		= homeCategories
		this.searchProfileBody 	= searchProfile
		this.currentSkill 		= skillTrending[0];
		this.trendingPeopleBasedOnSkill();
		this.searchProjectByCategory({ name: 'Technology', id: 16 });
	}

	ngOnDestroy() {
		if (this.interval1 || this.interval2) {
			clearInterval(this.interval1);
			clearInterval(this.interval2);
		}
	}

	getRecentActivities() {
		this.NotificationService.recentActivities()
		.subscribe(activities => {
			this.activities = activities
		});
	}

	trendingPeopleBasedOnSkill() {
		const length = this.skillTrending.length

		for (let i = 0; i < length; i++) {
			this.searchSkilledPeople(this.skillTrending[i]);
		}

		this.interval1 = setInterval( () => {
			this.count += 1
			if (this.count >= this.skillTrending.length)
				this.count = 0
			this.currentProfiles 	= this.skillProfiles[this.count];
			this.currentSkill 		= this.skillTrending[this.count];
		}, 4000);
	}

	searchProjectByCategory(category) {
		this.currentCategory 							= category.name
		this.searchProjectBody.query.members[1].value 	= category.name
		this.searchProjectBody.paginate.limit 			= 1
		this.ProjectsService.searchProject(this.searchProjectBody).subscribe( res => {
			this.bestProject = res.projects[0]
		});
	}

	searchSkilledPeople(skill) {
		const array = []
		array.push(skill);

		this.searchProfileBody.query.members[0].value 	= array
		this.searchProfileBody.paginate.limit 			= 5
		this.ProfilesService.searchProfiles(this.searchProfileBody).subscribe( res => {
			this.skillProfiles.push(res.profiles);
			if (!this.skillProfiles[1])
				this.currentProfiles = this.skillProfiles[0];
		});
	}

	followProject(id, follow) {
		if (!this.TokenService.getToken())
			return this.showLoginPopOver();

		this.ProjectsService.followProject(id, {}).subscribe( res => {
			if (!follow) {
				this.bestProject['follow'] = 1
				this.bestProject['followers'] += 1
			} else {
				this.bestProject['follow'] = null
				this.bestProject['followers'] -= 1
			}
		});
	}

	moveSliders() {
		this.interval1 = setInterval(() => {
			this.countSlide += 1
			if (this.countSlide > 4)
				this.countSlide = 1
			this.slideState = 'left' + this.countSlide.toString();
		}, 3000);
	}

	/* SHOW MODAL */
	showLoginPopOver() {
        let disposable 	= this.dialogService.addDialog(PopoverLoginComponent, {});
	}

	/* TRANSFORM FUNCTION */
	transformUrl(url) {
		url = url.replace(/ /g, '-');
		return url;
	}

	transformImage(url: string, width: number, height: number, crop: string) {
		return this.PicturesService.transformImage(url, width, height, crop);
	}

	/**************** MOBILE ***************/


}



