import { Component, OnInit, Input, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/platform-browser';

/* Libraries */
import { PageScrollConfig, PageScrollService, PageScrollInstance } from 'ng2-page-scroll';

declare let twttr : any

@Component({
	selector: 'app-home-static',
	templateUrl: './home-static.component.html',
	styleUrls: ['./home-static.component.scss'],
	providers: [PageScrollService]
})
export class HomeStaticComponent implements OnInit {

	@Input() profile 	: Object
	@Input() location 	: string
	@Input() mobile  	: boolean

	constructor(@Inject(DOCUMENT) private document: any) {
		setTimeout(() => {
			twttr.widgets.load(this.document.getElementById("tw-follow"));
		}, 3000)

		PageScrollConfig.defaultDuration = 1000;
		PageScrollConfig.defaultScrollOffset = 50;
        PageScrollConfig.defaultEasingLogic = {
            ease: (t: number, b: number, c: number, d: number): number => {
                // easeInOutExpo easing
                if (t === 0) return b;
                if (t === d) return b + c;
                if ((t /= d / 2) < 1) return c / 2 * Math.pow(2, 10 * (t - 1)) + b;
                return c / 2 * (-Math.pow(2, -10 * --t) + 2) + b;
            }
        };
	}

	ngOnInit() {
	}

}



