import { Component, OnInit, Input, OnDestroy } from '@angular/core';

/* Services */
import { ProjectsService } from '../../../Services/Projects/projects.service';
import { PicturesService } from '../../../Services/Pictures/pictures.service';
import { SharedService } from '../../../Services/shared.service';
import { ProfilesService } from '../../../Services/Profiles/profiles.service';

/* Interfaces */
import { SearchModel } from '../../../Interfaces/search-models';
import { searchProfile } from '../../../Interfaces/Constants/search-profile.constant';
import { searchProject, skillTrending } from '../../../Interfaces/Constants/search-project.constant';

@Component({
	selector: 'app-home-post',
	templateUrl: './home-post.component.html',
	styleUrls: ['./home-post.component.scss'],
	providers: [ProjectsService]

})
export class HomePostComponent implements OnInit, OnDestroy {

	@Input() mobile 	: boolean
	searchProjectBody 	: SearchModel
	searchProfileBody 	: SearchModel
	skillProjects		: Object[] = []
	currentProjects 	: any
	skillProfiles 		: Object[] = []
	currentProfiles 	: any
	skillTrending 		: Array<string> = skillTrending
	currentSkill 		: string
	count 				: number = 0
	interval1 			: any
	intervalMobile 		: any

	constructor(private ProjectsService: ProjectsService,
				private PicturesService: PicturesService,
				private ProfilesService: ProfilesService,
				private SharedService: SharedService) {
	}

	/* NG */
	ngOnInit() {
		this.searchProjectBody 	= searchProject
		this.searchProfileBody 	= searchProfile
		this.currentSkill 		= skillTrending[0];
		this.trendingProjectsBasedOnSkill();
		this.trendingProfilesBasedOnSkillMobile();
	}

	ngOnDestroy() {
		if (this.interval1)
			clearInterval(this.interval1);
	}

	/* SEARCH FUNCTION */
	searchSkilledProjects(skill) {
		const array = []
		array.push(skill);
		this.searchProjectBody.query.members[3].value 	= array
		this.searchProjectBody.paginate.limit 			= 14
		this.ProjectsService.searchProject(this.searchProjectBody).subscribe( res => {
			this.skillProjects.push(res.projects);
			if (!this.skillProjects[1])
				this.currentProjects = this.skillProjects[0];
		});
	}

	searchSkilledPeople(skill) {
		if (this.mobile) {
			const array = []
			array.push(skill);
			this.searchProfileBody.query.members[0].value 	= array
			this.searchProfileBody.paginate.limit 			= 5
			this.ProfilesService.searchProfiles(this.searchProfileBody).subscribe( res => {
				this.skillProfiles = res.profiles
				if (!this.skillProfiles[1])
					this.currentProfiles = this.skillProfiles[0];
			});
		}
	}

	/* TRENDING INTERVAL */
	trendingProfilesBasedOnSkillMobile() {
		if (this.mobile) {
			const length = this.skillTrending.length

			for (let i = 0; i < length; i++) {
				this.searchSkilledPeople(this.skillTrending[i]);
			}
			this.intervalMobile = setInterval( () => {
				this.count += 1
				if (this.count >= this.skillTrending.length)
					this.count = 0
				this.currentProfiles 	= this.skillProfiles[this.count];
				this.currentSkill 		= this.skillTrending[this.count];
			}, 5000)
		}
	}

	trendingProjectsBasedOnSkill() {
		const length = this.skillTrending.length

		for (let i = 0; i < length; i++) {
			this.searchSkilledProjects(this.skillTrending[i]);
		}
		this.interval1 = setInterval( () => {
			this.count += 1
			if (this.count >= this.skillTrending.length)
				this.count = 0
			this.currentProjects 	= this.skillProjects[this.count];
			this.currentSkill 		= this.skillTrending[this.count];
		}, 5000)
	}

	/* TRANSFORM */
	transformUrl(url) {
		url = url.replace(/ /g, '-');
		return url;
	}

	transformImage(url: string, width: number, height: number, crop: string) {
		return this.PicturesService.transformImage(url, width, height, crop);
	}

	scrollTop() {
		window.scrollTo(0, 0);
		this.SharedService.postActiveSignal(true);
	}
}



