import { Component, OnInit, ViewChild, ElementRef, Input, Output, EventEmitter, ChangeDetectorRef, OnDestroy } from '@angular/core';
import { Subscription }   from 'rxjs/Subscription';
import { DialogService } from "ng2-bootstrap-modal";
import { CloudinaryOptions, CloudinaryUploader } from 'ng2-cloudinary';
import { Router, ActivatedRoute } from '@angular/router';

/* Components */
import { PopoverLoginComponent } from '../../modals/popover-login/popover-login.component';
import { SkillModalComponent } from '../../modals/skill-modal/skill-modal.component';

/* Services */
import { SharedService } from '../../../Services/shared.service';
import { TokenService } from '../../../Services/Authentication/token.service';
import { ProjectsService } from '../../../Services/Projects/projects.service';
import { SkillsService } from '../../../Services/Skills/skills.service';
import { PicturesService } from '../../../Services/Pictures/pictures.service';

/* Models */
import { categories } from '../../../Interfaces/Constants/categories-constant';
import { needStatus2 } from '../../../Interfaces/Constants/status-constant';

/* Animtations */
import { slidePost } from '../../../Animations/slides.animation';
import { slideOnClick } from '../../../Animations/slides2.animation';

/* Libraries */
import * as _ from 'underscore'

export const stateSlide = {
	'0': 'initial',
	'1': 'outRight',
	'2': 'outRight',
	'3': 'outRight',
	'4': 'outRight',
	'5': 'outRight',
	'6': 'outRight',
	'7': 'outRight',
	'8': 'outRight',
	'9': 'outRight',
	'10': 'outRight'
}

@Component({
	selector: 'app-home-post-project',
	templateUrl: './home-post-project.component.html',
	styleUrls: ['./home-post-project.component.scss'],
	animations: [slidePost, slideOnClick]
})
export class HomePostProjectComponent implements OnInit, OnDestroy {

	@Input() login 		: number
	@Input() profile 	: Object
	@Output() slide		: EventEmitter<any> = new EventEmitter<any>()
	@ViewChild('autoFocus_1') input_1: ElementRef;
	@ViewChild('autoFocus_2') input_2: ElementRef;
	@ViewChild('autoFocus_3') input_3: ElementRef;
	@ViewChild('autoFocus_4') input_4: ElementRef;
	@ViewChild('autoFocus_5') input_5: ElementRef;
	@ViewChild('autoFocus_6') input_6: ElementRef;

	city 			: string
	state 			: string
	country 		: string
	title           : string
	app 			: string
	logo 			: string
	video 			: string
	type 			: string
	skillName 		: string
	tagLine 		: string = ''
	resume_1 		: string = ''
	resume_2 		: string = ''
	http_url 		: string = ''
	need_about 		: string = ''
	need_position 	: string = ''
	leftPixelStr 	: string = '0px'
	currentStatus   : string = 'Idea'
	currentStatus2 	: string = 'for help'
	currentCategory : string = 'Any category'
	currentCategoryId : number
	opening_id 		: number
	leftPixel 		: number = 0
	long 			: number
	lat 			: number
	project_id 		: number
	barWidth 		: number
	done 			: boolean = false
	uploading 		: boolean = false
	uploading1 		: boolean = false
	uploading2 		: boolean = false
	verify1			: boolean = false
	verify2 		: boolean = false
	verify3 		: boolean = false
	verify4 		: boolean = false
	pictures 		: Array<string> = []
	modals 			: Object
	subscription 	: Subscription
	categories      : Object[] = categories
	statutes 		: Object[] = needStatus2
	skills 			: Object[]
	selectedSkills 	: Object[] = []

	/* COUNTER */
	showComplete: boolean 	= false
	count 		: number 	= 0
	countTo 	: number 	= 0
	countFrom 	: number 	= 0

	mediaFile: any;

	uploader: CloudinaryUploader = new CloudinaryUploader(
		new CloudinaryOptions({ cloudName: 'dqpkpmrgk', uploadPreset: 'z7rzegb5', autoUpload: true })
	);

	constructor(private SharedService: SharedService,
				private TokenService: TokenService,
				private dialogService: DialogService,
				private ProjectsService: ProjectsService,
				private SkillsService: SkillsService,
				private PicturesService: PicturesService,
				private route: ActivatedRoute,
				private router: Router,
				private ChangeDetectorRef: ChangeDetectorRef) {
		this.imageUpload();
	}

	ngOnInit() {
		this.modals = stateSlide
		// this.initServices();
	}

	ngOnDestroy() {
		this.modals[0] = 'initial'
		this.modals[9] = 'outRight'
	}

	countComplete(index) {

		if (index === 0) {
			this.countTo 	= 20
		} else if (index === 1 && this.countFrom < 20) {
			this.countFrom 	= 20
			this.countTo 	= 30
		} else if (index === 2 && this.countFrom < 30) {
			this.countFrom 	= 30
			this.countTo 	= 40
		} else if (index === 3 && this.countFrom < 40) {
			this.countFrom 	= 40
			this.countTo 	= 50
		} else if (index === 4 && this.countFrom < 50 ) {
			this.countFrom 	= 50
			this.countTo 	= 55
		} else if (index === 5 && this.countFrom < 55) {
			this.countFrom 	= 55
			this.countTo 	= 60
		} else if (index === 6 && this.countFrom < 60) {
			this.countFrom 	= 60
			this.countTo 	= 80
		} else if (index === 7 && this.countFrom < 80) {
			this.countFrom 	= 80
			this.countTo 	= 90
		} else if (index === 8 && this.countFrom < 90) {
			this.countFrom 	= 90
			this.countTo 	= 95
		} else if (index === 9 && this.countFrom < 95) {
			this.countFrom 	= 90
			this.countTo 	= 100
		}
	}

	autoFocus(index) {
		if (index === 1)
			this.input_1.nativeElement.focus();
		else if ( index === 2)
			this.input_2.nativeElement.focus();
		else if (index === 3)
			this.input_3.nativeElement.focus();
		else if (index === 4)
			this.input_4.nativeElement.focus();
		// else if (index === 7)
		// 	this.input_5.nativeElement.focus();
		else if (index === 8)
			this.input_5.nativeElement.focus();
		else if (index === 9)
			this.input_6.nativeElement.focus();
	}

	// initServices() {
	// 	this.SkillsService.getSkills().subscribe( res => {
	// 		this.skills = res.skills
	// 	})
	// }

	getStatus(name) {
		this.currentStatus = name
		this.checkField();
	}

	getCategory(name, id) {
		this.currentCategory 	= name
		this.currentCategoryId 	= id
		this.checkField();
	}

	getAddress(place: Object) {
		const address 	= place['formatted_address'].replace('USA', 'United States')
		const array 	= address.split(', ');
		this.city 		= array[0]
		this.state 		= array[1]
		this.country 	= array[2] ? array[2] : array[1]
		this.long 		= place['geometry'].location.lat();
		this.lat 		= place['geometry'].location.lng();
		this.checkField();
		this.ChangeDetectorRef.detectChanges();
	}

	onChange(value) {
		if (value.length === 1)
			this.http_url = 'http://' + value
	}

	watchChange(value) {
		this.checkField();
	}

	verifyProject1(index, offset) {
		if (!this.TokenService.getToken())
			this.showLoginPopOver();
		else {
			if (this.currentCategoryId && this.currentStatus && this.title && this.city) {
				this.showComplete = true
				this.slideModals(index, offset);
				this.countComplete(index);
			} else
				console.log("ERROR ENTRY DATA");
		}
	}

	verifyProject2(index, offset) {
		if (this.tagLine) {
			this.slideModals(index, offset);
			this.countComplete(index);
		} else
			console.log("ERROR ENTRY DATA");
	}

	verifyProject3(index, offset) {
		if (this.resume_1) {
			this.slideModals(index, offset)
			this.countComplete(index);
		} else
			console.log("ERROR ENTRY DATA");
	}

	verifyProject4(index, offset) {
		if (this.resume_2) {
			this.createProject(index, offset);
			this.countComplete(index);
		} else
			console.log("ERROR ENTRY DATA");
	}

	checkField() {
		if (this.currentCategoryId && this.currentStatus && this.title && this.city)
			this.verify1 = true
		else
			this.verify1 = false
		this.verify2 = this.tagLine 	? true : false
		this.verify3 = this.resume_1 	? true : false
		this.verify4 = this.resume_2 	? true : false

	}

	selectSkill(name: string) {
		if (this.selectedSkills.indexOf(name) < 0 && this.selectedSkills.length < 5) {
			this.selectedSkills.push(name);
			this.skillName = null
		}
	}

	selectStatus(name: string) {
		if (name === 'Freelance' || name === 'Fundings') return
		this.currentStatus2 = name
	}

	removeSkill(name: string) {
		this.selectedSkills = _.without(this.selectedSkills, name);
	}

	createProject(index, offset) {
		if (!this.done) {
			const body = {
				title      	: this.title,
				status     	: this.currentStatus,
				category   	: this.currentCategoryId,
				description : this.tagLine,
				desc_1 		: this.replaceNewline(this.resume_1),
				desc_2 		: this.replaceNewline(this.resume_2),
				location   	: {
					city 	: 'San Francisco',
					state 	: 'CA',
					country : 'United States',
					lng 	: 37.7738997,
					lat 	: -122.4158846
				}
			}
			this.ProjectsService.createProject(body).subscribe( res => {
				if (res.id) {
					this.project_id = res
					this.done 		= true
					this.slideModals(index, offset);
					this.countComplete(index);
				}
			});
		} else {
			this.slideModals(index, offset);
		}
	}

	updateProject(index, offset) {
		const body = {
			title      	: this.title,
			// status     	: this.currentStatus,
			category_id : this.currentCategoryId,
			about 		: '',
			description : this.tagLine,
			desc_1 		: this.replaceNewline(this.resume_1),
			desc_2 		: this.replaceNewline(this.resume_2),
			video 		: this.video,
			link 		: this.http_url || '',
			app 		: this.app || '',
			logo 		: this.logo || '',
			picture 	: this.pictures[0],
			pictures 	: this.pictures,
			location   	: {
				city 	: 'San Francisco',
				state 	: 'CA',
				country : 'United States',
				lng 	: 37.7738997,
				lat 	: -122.4158846
			}
		}
		this.ProjectsService.updateProject(this.project_id['id'], body).subscribe( res => {
			this.slideModals(index, offset);
			this.countComplete(index);
		});
	}

	createProjectNeed(index, offset) {
		const body = {
			status 		: this.currentStatus2,
			description : this.replaceNewline(this.need_about),
			tags 		: this.selectedSkills
		}

		// if (body.tags[0] || body.status === 'Feedback') {
			this.ProjectsService.createProjectOpening(this.project_id['id'], body).subscribe( res => {
				if (res.id) {
					this.opening_id = res.id
					if (body.status === 'to hire someone' || body.status === 'to hire an intern')
						this.slideModals(index, offset);
					else
						this.slideModals(index, offset + 1);
					this.countComplete(index);
				}
			})
		// } else {
		// 	if (body.status === 'to hire someone' || body.status === 'to hire an intern')
		// 		this.slideModals(index, offset);
		// 	else
		// 		this.slideModals(index, offset + 1);
		// 	this.countComplete(index);
		// }
	}

	updateProjectNeed(index, offset) {
		if (this.opening_id) {
			const body = {
				status 		: this.currentStatus2,
				position 	: this.need_position,
				description : this.replaceNewline(this.need_about),
				tags 		: this.selectedSkills
			}

			if (body.status !== 'to hire someone' && body.status !== 'to hire an intern')
				body.position = '';

			this.ProjectsService.updateProjectOpening(this.opening_id, body).subscribe( res => {
				this.slideModals(index, offset);
				this.countComplete(index);
			});
		} else {
			this.slideModals(index, offset);
			this.countComplete(index);
		}
	}

	slideOnClick(side) {
		if (this.pictures.length) {
			if (side === 'left') {
				// const limit 	= 0
				this.leftPixel += 165
				if (this.leftPixel <= 0)
					this.leftPixelStr = this.leftPixel.toString() + 'px'
				else
					this.leftPixel -= 165
			} else {
				const limit 	= -165 * (this.pictures.length - 1);
				this.leftPixel -= 165
				if (this.leftPixel >= limit)
					this.leftPixelStr = this.leftPixel.toString() + 'px'
				else
					this.leftPixel += 165
			}
		}
	}

	slideModals(index, offset) {
		if (index < offset) {
			this.modals[index] 	= 'outLeft'
			this.modals[offset] = 'initial'
		} else if (index > offset) {
			if (index === 9 && (this.currentStatus2 !== 'to hire someone' && this.currentStatus2 !== 'to hire an intern'))
				offset -= 1
			this.modals[index] 	= 'outRight'
			this.modals[offset] = 'initial'
		}
		if (offset === 10) {
			setTimeout( () => {
				this.router.navigate(['/project', this.project_id['public_id'], this.title.replace(/ /g, '-')]);
			}, 2000)
		}

		this.autoFocus(offset);
	}

	/* UPLOAD */
	imageUpload() {
		this.uploader.onSuccessItem = (item: any, response: string, status: number, headers: any) => {
			this.uploading 	= false
			this.uploading1 = false
			this.uploading2 = false
			this.mediaFile 	= JSON.parse(response);

			if (this.type === 'pictures') {
				this.pictures.push(this.mediaFile.secure_url)
				this.slideOnClick('right');
			} else if (this.type === 'logo') {
				this.PicturesService.uploadPicture('project', 'logo', { url: this.mediaFile.secure_url })
					.subscribe(res => {
						if (res.picture)
							this.logo = res.picture
					});
			} else
				this.video 			= this.mediaFile.secure_url
		};

		this.uploader.onAfterAddingFile = (fileItem) => {
			if (this.type === 'logo')
				this.uploading 	= true
			else if (this.type === 'pictures')
				this.uploading2 = true
			else
				this.uploading1 = true
			this.uploader.uploadAll();
		};

	}

	setType(type) {
		this.type = type
	}

	removeMedia(type, index) {
		if (type === 'logo')
			this.logo = ''
		else if (type === 'pictures') {
			this.pictures.splice(index, 1);
			this.slideOnClick('left');
		} else
			this.video = ''
	}

	hidePost() {
		this.slide.emit();
	}

	transformImage(url: string, width: number, height: number, crop: string) {
		return this.PicturesService.transformImage(url, width, height, crop);
	}

	replaceNewline(text) {
		return text.replace(/\r?\n/g, '<br />');
	}

	/* SHOW MODAL */
	showLoginPopOver() {
        let disposable 	= this.dialogService.addDialog(PopoverLoginComponent, {

	    });
	}

	showSkillModal() {
		let disposable 	= this.dialogService.addDialog(SkillModalComponent, {
			selectedSkills : this.selectedSkills
		}).subscribe((skills: Array<string>) => {
			this.selectedSkills = skills;
			// if (!skills[0])
		});
	}

}



