import { trigger, state, animate, style, transition, keyframes, AnimationEntryMetadata } from '@angular/core';

/* Slide Right/Left dialog */
export const slidePost = trigger('slidePost', [
			state('initial', style({
				opacity: 1, left: '0px', right: '0px'
			})),
	    	state('outLeft', style({
	    		opacity: 0, left: '-1300px', right: '0px'
	    	})),
	    	state('inLeft', style({
	    		opacity: 1, left: '0px', right: '0px'
	    	})),
	    	state('outRight', style({
	    		opacity: 0, right: '-1300px', left: '0px',
	    	})),
	    	state('inRight', style({
	    		opacity: 1, right: '0px', left: '0px',
	    	})),
	    	transition('initial => outLeft', animate('250ms')),
	    	transition('outLeft => initial', animate('250ms 200ms')),
			transition('initial => outLeft', animate('250ms')),
			transition('outRight => initial', animate('250ms 200ms')),
			transition('initial => outRight', animate('250ms'))
	    ]);

