import { Component, OnInit, Input, HostListener } from '@angular/core';
import { DialogService } from "ng2-bootstrap-modal";

/* Components */
// import { GoogleContactModalComponent } from '../../modals/google-contact-modal/google-contact-modal.component';
import { InvitationSocialComponent } from '../../modals/invitation-social/invitation-social.component';

/* Services */
import { InvitationService } from '../../../Services/Invitation/invitation.service';

@Component({
	selector: 'app-home-invitation',
	templateUrl: './home-invitation.component.html',
	styleUrls: [
		'../home-static/home-static.component.scss',
		'./home-invitation.component.scss'
	]
})
export class HomeInvitationComponent implements OnInit {

	@Input() mobile : boolean

	buttonText 		: string = 'Send invites'
	emailsEntrance 	: string

	topPixelStr  	: string
	topPixelStr2 	: string

	topCount 		: number = 0
	topCount2 		: number = 0
	startY 			: number = 0
	initScroll  	: number = 0

	constructor(private InvitationService: InvitationService,
				private dialogService: DialogService) { }

	ngOnInit() {
	}

	showInvitationModal() {
		window.scrollTo(0, 0);
		let disposable 	= this.dialogService.addDialog(InvitationSocialComponent, {
        	check : true
    	}).subscribe( success => {

    	});
	}

	@HostListener("window:scroll", [])
	onWindowScroll() {
		if (this.initScroll === 0) {
			this.initScroll = 1
			this.startY = window.pageYOffset
		}
		if (this.startY <= window.pageYOffset) {
			this.topPixelStr 	= this.topCount-- + 'px';
			this.topPixelStr2 	= (this.topCount2 -= 1.5) + 'px'
			this.startY 		= window.pageYOffset
		} else {
			this.topPixelStr 	= this.topCount++ + 'px';
			this.topPixelStr2	= (this.topCount2 += 1.5) + 'px';
			this.startY 		= window.pageYOffset
		}
	}

	// showSocialModal() {
	// 	let disposable 	= this.dialogService.addDialog(GoogleContactModalComponent, {
 //        	profile 	: {},
 //        	index 		: 3
 //    	}).subscribe( success => {
 //    		console.log(success);
 //    	});
	// }

	parsingEmails() {
		const reg 		= /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
		let emails 		= this.emailsEntrance.replace(/\s+/g, '').split(',');
		let length 		= emails.length
		let newMails 	= []

		for (let i = 0; i < length; i++) {
			if (reg.test(emails[i])) {
				newMails.push(emails[i]);
			}
		}

		return newMails
	}

	resetButton() {
		this.buttonText 		= 'Sent'
		this.emailsEntrance		= ''
		setTimeout( () => {
			this.buttonText = 'Send invites'
		}, 2000);
	}

	sendInvitations() {
		if (this.emailsEntrance) {
			this.InvitationService.sendInvitation({ mail: this.parsingEmails() }).subscribe( res => {
				if (res.success) {
					this.resetButton();
				}
			});
		}
	}
}



