import { Component, OnInit, AfterViewInit, ViewChild, Input, ElementRef, HostListener } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { Router, ActivatedRoute, Params, Data, NavigationEnd } from '@angular/router';
import { Title, DOCUMENT } from '@angular/platform-browser';

/* Services */
import { AnimationService } from 'css-animator';
import { ProjectsService } from '../../Services/Projects/projects.service';
import { SharedService } from '../../Services/shared.service'
import { TokenService } from '../../Services/Authentication/token.service';
import { ProfilesService } from '../../Services/Profiles/profiles.service';
import { MetaService } from '../../Services/meta.service';
import { TrackingService } from '../../Services/Trackings/tracking.service';

/* Models */
import { categories } from '../../Interfaces/Constants/categories-constant';
import { countries } from '../../Interfaces/Constants/countries-constant';

/* Animations */
import { slidePost } from '../../Animations/slides.animation';

@Component({
	selector: 'app-home',
	templateUrl: './home.component.html',
	styleUrls: ['./home.component.scss'],
	animations: [slidePost],
	providers: [AnimationService, ProfilesService]
})
export class HomeComponent implements OnInit, AfterViewInit {

	@ViewChild('autoFocus') input: ElementRef;

	config: Object = {
		direction             : 'horizontal',
		speed                 : 400,
		autoplay              : 8000,
		spaceBetween          : 30,
		loop                  : true,
		pagination            : '.swiper-pagination',
		paginationClickable   : true,
		paginationHide        : false,
	}

	animates        : boolean = false
	projects        : Array<string>
	state1 			: string = 'initial'
	state2 			: string = 'outRight'
	subscription 	: Subscription
	mobile 			: boolean = false
	login 			: number = 0
	profile 		: Object
	check 			: boolean = false
	location		: string
	// check2 			: boolean = false
	// runNumber 		: boolean = false

	// OLD PROJECT

	city 			: string
	state 			: string
	country 		: string
	categories      : Object[] = categories
	title           : string
	currentStatus   : string = 'Idea'
	currentCategory : string = 'Any category'
	currentCategoryId : number
	long 			: number
	lat 			: number

	constructor (private ProjectsService: ProjectsService,
				private SharedService: SharedService,
				private route: ActivatedRoute,
				private router: Router,
				private TokenService: TokenService,
				private ProfilesService: ProfilesService,
				private Title: Title,
				private MetaService: MetaService,
				private TrackingService: TrackingService) {

		if ((window.screen.width) < 736)
			this.mobile = true

		this.subscription = this.SharedService.postActive$.subscribe( res => {
			if (res) {
				this.state1 	= 'outLeft'
				this.state2 	= 'initial'
			}
		});

		this.getToken();
	}

	ngOnInit() {
		this.getProfile();
		this.getLocation();
		this.setMetaData();
		this.trackViewPage();
	}

	getLocation() {
		setTimeout(() => {
			if (!this.location)
				this.location = 'San Francisco, CA, United Stated'
		}, 3000);

		// this.ProfilesService.getIP().subscribe(res => {
		// 	let city, state, country;

		// 	city 	= res.city + ', '
		// 	country = res.country_name;

		// 	if (res.region_code)
		// 		state = res.region_code + ', '
		// 	else
		// 		state = "' ', "

		// 	this.location = city + state + country
		// });
	}

	trackViewPage() {
		const token = this.TokenService.getToken();

		this.TrackingService.viewActivities({}, token, 5).subscribe(r => {});
	}


	setMetaData() {
		this.Title.setTitle('Witty | Networking for the entrepreneurial age');
		this.MetaService.setMeta('description', 'We give exposure to all the ideas, projects and startups from around the world and connect them to the people they need, instantly');

		this.MetaService.setMeta('og:title', "Witty - Networking for the entrepreneurial age");
		this.MetaService.setMeta('og:description', 'We showcase all the ideas, products, initiatives and startups from around the world and connect them to all the people they need, including you.');
		this.MetaService.setMeta('og:url', 'https://www.wittycircle.com');
		this.MetaService.setMeta('og:image', 'http://res.cloudinary.com/dqpkpmrgk/image/upload/v1508448660/Share_Link_Cards_Facebook/Folger_Coffee_Company_Building__San_Francisco_2.jpg');
	}

	ngAfterViewInit() {
		this.animates = true
	}

	onResize(event) {
		if (event.target.innerWidth < 736)
			this.mobile = true
		else
			this.mobile = false
	}

	scrollToTop() {
		this.router.events.subscribe((evt) => {
            if (!(evt instanceof NavigationEnd)) {
                return;
            }
            window.scrollTo(0, 0)
        });
	}

	getProfile() {
		if (this.login) {
			this.ProfilesService.getProfile(this.login).subscribe( res => {
				this.profile = res.profile
			});
		}
	}

	getToken() {
		if (this.TokenService.getToken())
			this.login = this.TokenService.getToken().user.id
	}

	getStatus(name) {
		this.currentStatus = name
	}

	getCategory(name, id) {
		this.currentCategory 	= name
		this.currentCategoryId 	= id
	}

	createProject() {
		if (!this.TokenService.getToken())
			return ;
		else {
			if (this.currentCategoryId && this.currentStatus && this.title && this.city) {
				const body = {
					title      : this.title,
					status     : this.currentStatus,
					category   : this.currentCategoryId,
					location   : {
						city 	: this.city,
						state 	: this.state,
						country : this.country,
						lng 	: this.long,
						lat 	: this.lat
					}
				}
				this.ProjectsService.createProject(body).subscribe( res => {
					if (res)
						this.router.navigate(['/project/update'], { queryParams: { id: res.id, start: 1 } });
				});
			} else
				console.log("ERROR");
		}
	}

	getAddress(place: Object) {
		const address 	= place['formatted_address'].replace('USA', 'United States')
		const array 	= address.split(', ');
		this.city 		= array[0]
		this.state 		= array[1]
		this.country 	= array[2] ? array[2] : array[1]
		this.long 		= place['geometry'].location.lat();
		this.lat 		= place['geometry'].location.lng();
	}

	showPost() {
		this.state1 	= 'outLeft'
		this.state2 	= 'initial'
	}

	hidePost(value) {
		this.state1 	= 'initial'
		this.state2 	= 'outRight'
	}

	// @HostListener("window:scroll", [])
	// onWindowScroll() {
	// 	let number = document.documentElement.scrollTop;

	// 	if (number >= 1100 && number < 1700 && !this.check2) {
	// 		this.runNumber = this.check2 = true
	// 	}
	// }
}



