import { Component, OnInit, Input } from '@angular/core';
import { DialogService } from "ng2-bootstrap-modal";

/* Components */
import { PopoverLoginComponent } from '../../modals/popover-login/popover-login.component';

/* Services */
import { ProjectsService } from '../../../Services/Projects/projects.service';
import { ProfilesService } from '../../../Services/Profiles/profiles.service';
import { TokenService } from '../../../Services/Authentication/token.service';
import { PicturesService } from '../../../Services/Pictures/pictures.service';
import { SuggestionService } from '../../../Services/Suggestions/suggestion.service';
import { BackofficeService } from '../../../Services/Backoffice/backoffice.service';

/* Interfaces */
import { ProjectCardModel } from '../../../Interfaces/search-models';
import { searchProject2 } from '../../../Interfaces/Constants/search-project.constant';

/* Libraries */
import { NgxSiemaOptions } from 'ngx-siema';

@Component({
	selector: 'app-home-projects',
	templateUrl: './home-projects.component.html',
	styleUrls: [
		'../home-static/home-static.component.scss',
		'./home-projects.component.scss',
		'../../../../public/styles/project-card.scss',
	]
})
export class HomeProjectsComponent implements OnInit {

	@Input() mobile 	: boolean
	@Input() profile	: Object
	@Input() location 	: string

	newProjects 	: ProjectCardModel[]
	oppProjects 	: ProjectCardModel[]
	body_project2 	: Object = searchProject2
	profileAbout 	: Array<string> = ['for a full time position', 'for an internship', "to share what I'm working on", 'for part time collaboration', 'to meet smart people']
	projectStatus 	: Array<string> = ['to hire someone', 'to hire an intern', 'for feedback', 'for a cofounder', 'for help']

	options: NgxSiemaOptions = {
	    selector: '.siema',
	    duration: 200,
	    easing: 'ease-out',
	    perPage: 1,
	    startIndex: 0,
	    draggable: true,
	    threshold: 20,
	    loop: false,
		onInit: () => {
			// runs immediately after first initialization
		},
		onChange: () => {
			// runs after slide change
		},
	};

	options2: NgxSiemaOptions = {
	    selector: '.siema',
	    duration: 200,
	    easing: 'ease-out',
	    perPage: 1,
	    startIndex: 0,
	    draggable: true,
	    threshold: 20,
	    loop: false,
		onInit: () => {
			// runs immediately after first initialization
		},
		onChange: () => {
			// runs after slide change
		},
	};


	constructor(private ProjectsService: ProjectsService,
				private TokenService: TokenService,
				private PicturesService: PicturesService,
				private dialogService: DialogService,
				private SuggestionService: SuggestionService,
				private ProfilesService: ProfilesService,
				private BackofficeService: BackofficeService) {
		// this.initSearchProject();
	}

	ngOnInit() {
		this.getNewProjects();
		this.getOpportunityProjects();
	}

	getProjects() {
		this.ProjectsService.searchProject(this.body_project2).subscribe( res => {
			this.oppProjects = res.projects.sort(() => .5 - Math.random()).slice(0, 3);
		});
	}

	getNewProjects() {
		this.BackofficeService.getSelectedProjectsProfiles('new_projects').subscribe(res => {
			this.newProjects = res.results;
		});
	}

	getOpportunityProjects() {
		const skills 	= this.profile['skills'].split(',').slice(0, 2);
		const about 	= this.projectStatus[this.profileAbout.indexOf(this.profile['about'])];

		this.body_project2['query']['members'][4].value 	= this.location
		this.body_project2['query']['members'][0]['value']	= about
		this.body_project2['query']['members'][3]['value'] 	= skills || [""]
		this.getProjects();
	}

	// followProject(id, index) {
	// 	if (!this.TokenService.getToken())
	// 		return this.showLoginPopOver();

	// 	this.ProjectsService.followProject(id, {}).subscribe( res => {
	// 		this.projects[index]['follow'] = res.success
	// 		if (res.success)
	// 			this.projects[index]['followers'] += 1
	// 		else
	// 			this.projects[index]['followers'] -= 1
	// 	});
	// }

	transformNeedStatus(status) {
		if (status === 'for help')
			return 'any help'
		else if (status === 'for feedback')
			return 'feedback'
		else if (status === 'to hire someone')
			return 'hiring'
		else if (status === 'to hire an intern')
			return 'hiring interns'
		else if (status === 'for a cofounder')
			return 'cofounder'
	}

	/* SHOW MODAL */
	showLoginPopOver() {
        let disposable 	= this.dialogService.addDialog(PopoverLoginComponent, {});
	}

	/* TRANSFORM */
	transformUrl(url) {
		url = url.replace(/ /g, '-');
		return url;
	}

	transformImage(url: string, width: number, height: number, crop: string) {
		return this.PicturesService.transformImage(url, width, height, crop);
	}

}



