import { Component, OnInit, ViewEncapsulation, Input } from '@angular/core';
import { DialogService } from "ng2-bootstrap-modal";

/* Components */
import { PopoverLoginComponent } from '../../modals/popover-login/popover-login.component';

/* Services */
import { ProjectsService } from '../../../Services/Projects/projects.service';
import { ProfilesService } from '../../../Services/Profiles/profiles.service';
import { PicturesService } from '../../../Services/Pictures/pictures.service';
import { TokenService } from '../../../Services/Authentication/token.service';
import { SuggestionService } from '../../../Services/Suggestions/suggestion.service';
import { BackofficeService } from '../../../Services/Backoffice/backoffice.service';

/* Interfaces */
import { ProfileModels } from '../../../Interfaces/profile-models';
import { searchProfile } from '../../../Interfaces/Constants/search-profile.constant';

import { NgxSiemaOptions } from 'ngx-siema';

@Component({
	selector: 'app-home-around',
	templateUrl: './home-around.component.html',
	styleUrls: [
		'../home-static/home-static.component.scss',
		'./home-around.component.scss',
		'../../../../public/styles/profile-card.scss',
		'../../../../public/styles/tooltip.scss'
	],
	encapsulation: ViewEncapsulation.None,
	providers: []
})
export class HomeAroundComponent implements OnInit {

	@Input() mobile 	: boolean
	@Input() profile	: Object
	@Input() location 	: string

	greatProfiles		: ProfileModels[]
	body_profile 		: Object = searchProfile
	suggestProfiles 	: Object[]
	recursiveIndex 		: number = 0
	secondLineCardTitle : string
	secondLineCardText 	: string
	profileAbout 		: Array<string> = ['for a full time position', 'for an internship', "to share what I'm working on", 'for part time collaboration', 'to meet smart people']

	options1: NgxSiemaOptions = {
	    selector: '.siema2',
	    duration: 200,
	    easing: 'ease-out',
	    perPage: 1,
	    startIndex: 0,
	    draggable: true,
	    threshold: 20,
	    loop: false,
		onInit: () => {
			// runs immediately after first initialization
		},
		onChange: () => {
			// runs after slide change
		},
	};

	constructor(private ProfilesService: ProfilesService,
				private ProjectsService: ProjectsService,
				private PicturesService: PicturesService,
				private TokenService: TokenService,
				private dialogService: DialogService,
				private SuggestionService: SuggestionService,
				private BackofficeService: BackofficeService) {
	}

	ngOnInit() {
		this.initSearchBody();
	}

	initSearchBody() {
		this.body_profile['paginate']['limit'] 			= 4
		this.body_profile['query']['members'][2].value 	= this.location
		if (this.profile['name']) // network
				this.body_profile['query']['members'][3].value = this.profile['name']
		this.getSelectedProfiles();
		this.getUserProject(0);
	}

	getProfiles() {
		this.ProfilesService.searchProfiles(this.body_profile).subscribe( res => {
			this.suggestProfiles = res.profiles
		});
	}

	getSelectedProfiles() {
		this.BackofficeService.getSelectedProjectsProfiles('great_people').subscribe(r => {
			this.greatProfiles = r.results
		});
	}

	getUserProject(index) {
		if (!this.profile)
			return ;
		else {
			this.ProjectsService.getUserProjects(this.profile['uid']).subscribe(r => {
				let lastCall = false

				if (!r.projects.length || index + 1 === r.projects.length)
					lastCall = true
				if (r.projects[index])
					this.getSuggestProfiles(r.projects[index].id, lastCall);
			});
		}
	}

	getShouldMeetProfiles() {
		this.secondLineCardTitle 	= `around <span>you</span>`
		this.secondLineCardText 	= `Here are some great people around you`

		if (this.profile['about'] === 'to meet smart people') {
			this.body_profile['query']['sort']['field'] 	= 'rank'
			this.body_profile['paginate']['offset'] 		= Math.floor((Math.random() * 30) + 1);
		} else if (this.profile['about'] === "to share what I'm working on") {
			this.body_profile['query']['members'][1].value 	= 'to meet smart people'
		} else {
			this.body_profile['query']['members'][0].value 	= this.profile['skills'].split(',')[0];
			this.body_profile['query']['members'][1].value 	= "to share what I'm working on"
		}

		this.getProfiles();
	}

	getSuggestProfiles(id, lastCall) {
		this.SuggestionService.profiles({ projectId : id }).subscribe(res => {
			if (res[0]) {
				this.secondLineCardTitle 	= `<span>people</span> who could <span>work</span> with you`
				this.secondLineCardText 	= `Here are some great people who could work with you`
				this.suggestProfiles 		= res.sort(() => .5 - Math.random()).slice(0, 4);
			} else if (!lastCall)
				this.getUserProject(this.recursiveIndex + 1);
			else
				this.getShouldMeetProfiles();
		}, error => {
			if (!lastCall)
				this.getUserProject(this.recursiveIndex + 1);
			else if (lastCall)
				this.getShouldMeetProfiles();
		});
	}

	// followProfile(id, index) {
	// 	if (!this.TokenService.getToken())
	// 		return this.showLoginPopOver();

	// 	this.ProfilesService.followProfile(id, {}).subscribe( res => {
	// 		if (!this.profiles[index]['hasLiked']){
	// 			this.profiles[index]['follower'] += 1
	// 			this.profiles[index]['hasLiked'] = true
	// 		} else {
	// 			this.profiles[index]['follower'] -= 1
	// 			this.profiles[index]['hasLiked'] = false
	// 		}
	// 	});
	// }

	transformCardStatus(status) {
		if (status === 'to meet smart people')
			return 'Meet smart people'
		else if (status === 'for a full time position')
			return 'Full time position'
		else if (status === 'for an internship')
			return 'Internship'
		else if (status === 'for part time collaboration')
			return 'Collaboration'
		else if (status === "to share what I'm working on")
			return 'Share work'
	}

	transformToArray(string) {
		if (typeof string === 'string')
			return string.split(',');
		else
			return string
	}

	/* SHOW MODAL */
	showLoginPopOver() {
        let disposable 	= this.dialogService.addDialog(PopoverLoginComponent, {});
	}

	/* TRANSFORM */
	transformImage(url: string, width: number, height: number, crop: string) {
		return this.PicturesService.transformImage(url, width, height, crop);
	}
}



