import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { PipeModule } from '../../Pipes/pipe.module';
import { FooterModule } from '../footer/footer.module';

/* Components */
import { HomeComponent } from './home.component';
import { HomeTrendingComponent } from './home-trending/home-trending.component';
import { HomeAroundComponent } from './home-around/home-around.component';
import { HomePostComponent } from './home-post/home-post.component';
import { ProfilesComponent } from '../profiles/profiles.component';

/* Directives */
import { HomeDirective } from '../../Directives/Home/home.directive';
import { DirectivesModule } from '../../Directives/directives.module';

/* Services */
import { PicturesService } from '../../Services/Pictures/pictures.service';
import { ProjectsService } from '../../Services/Projects/projects.service';
import { ProfilesService } from '../../Services/Profiles/profiles.service';
import { InvitationService } from '../../Services/Invitation/invitation.service';
import { SkillsService } from '../../Services/Skills/skills.service';
// import { MetaService } from '../../Services/meta.service';
import { BackofficeService } from '../../Services/Backoffice/backoffice.service';

/* Library */
import { SwiperModule, SwiperConfigInterface } from 'ngx-swiper-wrapper';
import { MomentModule } from 'angular2-moment';
import { Ng2CloudinaryModule } from 'ng2-cloudinary';
import { FileUploadModule } from 'ng2-file-upload';
import { Ng2PageScrollModule } from 'ng2-page-scroll';
// import { CountoModule }  from 'angular2-counto';
import { NgxSiemaModule } from 'ngx-siema';

/* Route */
import { HOME_ROUTES } from '../../Routes/home.routes';
import { HomePostProjectComponent } from './home-post-project/home-post-project.component';
import { HomeProjectsComponent } from './home-projects/home-projects.component';
import { HomeInvitationComponent } from './home-invitation/home-invitation.component';
import { HomeStaticComponent } from './home-static/home-static.component';

const SWIPER_CONFIG: SwiperConfigInterface = {
    direction: 'horizontal',
    slidesPerView: 'auto',
    keyboardControl: true,
    speed: 300,
};

@NgModule({
	imports: [
		HOME_ROUTES,
		CommonModule,
		FormsModule,
		RouterModule,
		FooterModule,
		SwiperModule.forRoot(SWIPER_CONFIG),
		PipeModule,
		MomentModule,
		DirectivesModule,
		Ng2CloudinaryModule,
		FileUploadModule,
		Ng2PageScrollModule.forRoot(),
		// CountoModule,
		NgxSiemaModule.forRoot()
	],
	exports: [
	],
	declarations: [
		HomeComponent,
		HomeAroundComponent,
		HomeTrendingComponent,
		HomeDirective,
		HomePostComponent,
		HomePostProjectComponent,
		HomeProjectsComponent,
		HomeInvitationComponent,
		HomeStaticComponent,
	],
	providers: [
		PicturesService,
		ProjectsService,
		ProfilesService,
		InvitationService,
		SkillsService,
		BackofficeService
	]
})
export class HomeModule { }



